import { css } from '@emotion/css';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Button,
  Card,
  Col,
  Input,
  Layout,
  Menu,
  Row,
  Space,
  Spin,
  Tree,
  Typography,
} from 'antd';
import { PlusCircleOutlined } from '@ant-design/icons';
import { Content, Footer } from 'antd/lib/layout/layout';
import { tree } from './tree.mock';
import PolicyResource from './role-policy.component';
import { roleResourceData } from './role-resource.data';
import { Resource } from './resource/resource.component';
import { useEffect, useState } from 'react';
import { useQuery } from '@apollo/client';
import { getRoleById } from './graphql/role.query';
import NotFoundPage from '@/components/not-found.component';

function AddRole(props) {
  const [currentResource, setCurrentResource] = useState('');
  const { data, loading, error } = useQuery(getRoleById, {
    variables: {
      where: {
        id: 1,
      },
    },
  });

  useEffect(() => {
    if (data) {
      setCurrentResource('group');
    }
  }, [data]);

  if (error) return <NotFoundPage />;
  if (loading || !currentResource)
    return (
      <div className="center-screen">
        <Spin size="large" />
      </div>
    );

  return (
    <div className={roleStyle}>
      <div className="add-role">
        <Typography.Title level={3}>Add Role</Typography.Title>
        <Space direction="vertical" style={{ marginBottom: 10 }}>
          <Input placeholder="Basic usage" />
        </Space>
      </div>

      <Row>
        <Col span={6}>
          <div className={resourceStyle}>
            <Card
              size="small"
              title={<Typography.Title level={4}>Resource</Typography.Title>}
              bodyStyle={{
                height: `calc(100vh - 100px - 20px - 48px - 48px - 45px)`,
              }}
            >
              <PerfectScrollbar>
                {/* <Tree
                  showIcon
                  // defaultExpandAll
                  defaultSelectedKeys={['0']}
                  // switcherIcon={<DownOutlined />}
                  treeData={roleResourceData}
                /> */}
                <Resource setCurrentResource={setCurrentResource} />
              </PerfectScrollbar>
            </Card>
          </div>
        </Col>
        <Col span={18}>
          <PolicyResource
            currentResource={currentResource}
            roleId={data.role.id}
          />
        </Col>
      </Row>
      <Footer
        style={{
          textAlign: 'center',
          position: 'sticky',
          bottom: 0,
          height: 48,
          padding: 0,
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
        }}
      >
        <Button danger> Cancel</Button>
        <Button type="primary">Save</Button>
      </Footer>
    </div>
  );
}

export default AddRole;

const roleStyle = css`
  max-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  & .add-role {
    height: 100px;
  }
  & .ant-row {
    height: 100%;
  }
`;
const resourceStyle = css`
  & .ant-card-body {
    padding-right: 0;
  }
`;
