import React, { useEffect, useState } from 'react';
import {
  Card,
  Table,
  Input,
  InputNumber,
  Popconfirm,
  Form,
  Typography,
  Switch,
  Button,
  Space,
  Spin,
} from 'antd';
import {
  DeleteOutlined,
  EditOutlined,
  PlusCircleOutlined,
  PlusOutlined,
} from '@ant-design/icons';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Select } from 'antd';
import { getRoleById, getRoleList } from './graphql/role.query';
import { useMutation, useQuery } from '@apollo/client';
import { getPolicyByRoleResource } from './graphql/policy.query';
import NotFoundPage from '@/components/not-found.component';
import { addPolicyMutation } from './graphql/policy.mutation';

const originData = [];

for (let i = 0; i < 10; i++) {
  originData.push({
    id: i.toString(),
    condition: `r.user.active == true`,
    action: Math.random() < 0.5 ? 'read' : 'write',
    effect: Math.random() < 0.5 ? 'allow' : 'deny',
    effectWith: Math.random() < 0.5 ? 'own' : 'any',
  });
}

const EditableCell = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  let Cell;
  switch (dataIndex) {
    case 'action':
      Cell = (
        <Form.Item
          name={dataIndex}
          initialValue={record.action}
          style={{
            margin: 0,
          }}
          rules={[
            {
              required: true,
              message: `Please Input ${dataIndex}!`,
            },
          ]}
        >
          <Select
            options={options.action}
            style={{ width: '100%' }}
            disabled={!editing}
          />
        </Form.Item>
      );
      break;
    case 'effectWith':
      Cell = (
        <Form.Item
          name={dataIndex}
          initialValue={record.effectWith}
          style={{
            margin: 0,
          }}
          rules={[
            {
              required: true,
              message: `Please Input ${dataIndex}!`,
            },
          ]}
        >
          <Select
            options={options.effectWith}
            style={{ width: '100%' }}
            disabled={!editing}
          />
        </Form.Item>
      );
      break;
    case 'effect':
      Cell = (
        <Form.Item
          name={dataIndex}
          initialValue={record.effect}
          style={{
            margin: 0,
          }}
          rules={[
            {
              required: true,
              message: `Please Input ${dataIndex}!`,
            },
          ]}
        >
          <Switch
            disabled={editing ? false : true}
            checked={record.effect == 'deny' ? true : false}
          />
        </Form.Item>
      );
      break;
    case 'condition':
    default:
      Cell = (
        <Form.Item
          name={dataIndex}
          style={{
            margin: 0,
          }}
          rules={[
            {
              max: 50,
              message: `Linit with 50 character`,
            },
          ]}
        >
          {inputType === 'number' ? <InputNumber /> : <Input />}
        </Form.Item>
      );
  }
  // const inputNode = inputType === 'number' ? <InputNumber /> : <Input />;

  return <td {...restProps}>{editing ? Cell : children}</td>;
};

const options = {
  action: [
    { label: 'Create', value: 'create' },
    { label: 'Read', value: 'read' },
    { label: 'Write', value: 'write' },
    { label: 'Delete', value: 'delete' },
  ],
  effectWith: [
    { label: 'Only Owner', value: 'own' },
    { label: 'Any', value: 'any' },
  ],
};
const PolicyResource = ({ currentResource, roleId }) => {
  const [form] = Form.useForm();
  const [policies, setPolices] = useState([]);
  const [editingKey, setEditingKey] = useState('');
  const [editingFromCreate, setEditingFromCreate] = useState(false);
  const [addPolicy, policyResult] = useMutation(addPolicyMutation);

  const { data, loading, error } = useQuery(getPolicyByRoleResource, {
    variables: {
      where: {
        resource: currentResource,
        roleId,
      },
    },
  });

  useEffect(() => {
    if (data) {
      setPolices(data.z_policyByRoleResource);
    }
  }, [data]);

  if (loading)
    return (
      <div className="center-screen">
        <Spin size="large" />
      </div>
    );

  if (error | policyResult.error) return <h1>error</h1>;

  const isEditing = (record) => record.id === editingKey;

  const handleAdd = () => {
    setPolices((prevData) => {
      const newData = {
        id: (prevData[0].id + 'z').toString(),
        conditon: '',
        action: 'read',
        effect: `allow`,
        effectWith: 'own',
      };

      setEditingKey(newData.id);
      setEditingFromCreate(true);

      return [newData, ...prevData];
    });
  };

  const handleEdit = (record) => {
    form.setFieldsValue({
      ...record,
    });
    setEditingKey(record.id);
  };

  const handleCancel = () => {
    form.setFieldsValue({
      condition: '',
      action: '',
      effect: '',
      effectWith: '',
    });
    setEditingKey('');
  };

  const handleDelete = (record) => {
    setPolices((prevData) => {
      setEditingKey('');
      setEditingFromCreate(false);
      const tmp = prevData.filter((data) => data.id !== record.id);
      return tmp;
    });
  };

  const handleSave = async (id) => {
    try {
      const row = await form.validateFields();
      await addPolicy({
        variables: {
          data: {
            object: currentResource,
            action: 'read',
            effect: `allow`,
            effectWith: 'own',
            condition: '',
          },
        },
      });

      const newData = [...policies];
      const index = newData.findIndex((item) => id === item.id);

      if (index > -1) {
        const item = newData[index];
        newData.splice(index, 1, { ...item, ...row });
      } else {
        newData.push(row);
      }
      setPolices(newData);
      setEditingKey('');
    } catch (errInfo) {
      console.log('Validate Failed:', errInfo);
    }
  };

  const columns = [
    {
      title: 'Action',
      dataIndex: 'action',
      width: '25%',
      editable: true,
      render: (action) => {
        return (
          <Select
            defaultValue={action}
            options={options.action}
            style={{ width: '100%' }}
            disabled={true}
          />
        );
      },
    },
    {
      title: 'Effect With',
      dataIndex: 'effectWith',
      width: '25%',
      editable: true,
      render: (action) => {
        return (
          <Select
            defaultValue={action}
            options={options.effectWith}
            style={{ width: '100%' }}
            disabled={true}
          />
        );
      },
    },
    {
      title: 'Deny',
      dataIndex: 'effect',
      width: '10%',
      align: 'center',
      editable: true,
      render: (effect) => {
        return (
          <Switch
            disabled={true}
            defaultChecked={effect == 'deny' ? true : false}
          />
        );
      },
    },
    {
      title: 'Condition',
      dataIndex: 'condition',
      width: '25%',
      editable: true,
    },
    {
      title: 'Operation',
      dataIndex: 'operation',
      align: 'center',
      render: (_, record) => {
        const editable = isEditing(record);
        return editable ? (
          <span>
            <Typography.Link
              onClick={() => handleSave(record.id)}
              style={{
                marginRight: 8,
              }}
            >
              Save
            </Typography.Link>
            <Popconfirm
              title="Sure to cancel?"
              onConfirm={() =>
                editingFromCreate ? handleDelete(record) : handleCancel(record)
              }
            >
              <a>Cancel</a>
            </Popconfirm>
          </span>
        ) : (
          <>
            <Space size={'middle'}>
              <Typography.Link
                disabled={editingKey !== ''}
                onClick={() => handleEdit(record)}
              >
                <EditOutlined />
              </Typography.Link>
              <Typography.Link
                disabled={editingKey !== ''}
                onClick={() => handleDelete(record)}
              >
                <DeleteOutlined />
              </Typography.Link>
            </Space>
          </>
        );
      },
    },
  ];

  const mergedColumns = columns.map((col) => {
    if (!col.editable) {
      return col;
    }

    return {
      ...col,
      onCell: (record) => ({
        record,
        inputType: col.dataIndex === 'age' ? 'number' : 'text',
        dataIndex: col.dataIndex,
        title: col.title,
        editing: isEditing(record),
      }),
    };
  });

  return (
    <Card
      size="small"
      title={<Typography.Title level={4}>Policy</Typography.Title>}
      extra={
        <Button
          size="small"
          disabled={editingKey !== ''}
          onClick={handleAdd}
          type="primary"
          shape="circle"
        >
          <PlusOutlined />
        </Button>
      }
      bodyStyle={{
        height: `calc(100vh - 100px - 20px - 48px - 48px - 45px)`,
      }}
    >
      <PerfectScrollbar>
        <Form form={form} component={false}>
          <Table
            components={{
              body: {
                cell: EditableCell,
              },
            }}
            pagination={false}
            size="small"
            dataSource={policies}
            columns={mergedColumns}
            rowClassName="editable-row"
            rowKey={(record) => record.id}
          />
        </Form>
      </PerfectScrollbar>
    </Card>
  );
};

export default PolicyResource;
